@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Open+Sans:wght@600&family=Poppins:wght@400;700;800&family=Roboto:wght@400;500;700&display=swap');

.main-sidebar-div {
    color: white;
    font-family: 'Roboto', sans-serif;
    margin-left: 15%;
    max-width: 300px;
    border-right: 1px solid rgba(255, 255, 255, 0.219);
    padding-top: 3%;
    padding-right: 3%;
}

.sidebar-sub-div {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: 300px;
    margin-bottom: 20%;
}

.description {
    text-align: right;
    width: 150px;
    line-height: 200%;
}


.image-div {
    background-color: rgba(255, 255, 255, 0.486);
    width: 60px;
    height: 60px;
    border-radius: 50%;
    text-align: center;
    align-items: center;
    line-height: 90px;
    margin-right: 20px;
    margin-left: 20px;
}

.image {
    width: 40px;
}


.small-dot {
    width: 16px;
    height: 16px;
    border: 2px solid rgba(255, 255, 255, 0.486);
    border-radius: 50%;
    margin-right: -7px;
    background-color: rgb(0, 0, 85);
}

.green-class {
    background-color: rgb(66, 182, 66);
}