@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Open+Sans:wght@600&family=Poppins:wght@400;700;800&family=Roboto:wght@400;500;700&display=swap');

.main-page4 {
    color: white;
    font-family: 'Roboto', sans-serif;
    word-wrap: normal;
    margin-top: 3%;
    margin-left: 3%;
    margin-bottom: 5%;
}

.main-page4 p {
    font-size: 15px;
    word-wrap: normal;
}

.page4-top {
    line-height: 40px;
    max-width: 600px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.219);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 4px;
    padding-bottom: 30px;

}

.page4-bottom {
    margin-top: 10%;
}

.page4-buttom {
    background-color: rgb(66, 182, 66);
    color: white;
    height: 50px;
    width: 120px;
    border-radius: 30px;
    margin-top: 30px;
    border: none;
    cursor: pointer;
}

.page1-buttom:hover {
    background-color: rgb(105, 201, 105);
}

.profile-picture {
    width: 200px;
    border-radius: 50%;
}

.github-link {
    text-decoration: none;
    color: rgb(75, 201, 240);
}


.profile-subdiv1 {
    padding-left: 30px;
}

.profile-subdiv1 h1 {
    margin-bottom: 30px;
}

.profile-subdiv2 {
    margin-top: 28%;
    padding-left: 50px;
}