@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Open+Sans:wght@600&family=Poppins:wght@400;700;800&family=Roboto:wght@400;500;700&display=swap');

.main-page1 {
    color: white;
    font-family: 'Roboto', sans-serif;
    word-wrap: normal;
    margin-top: 1%;
    margin-left: 2%;
    margin-right: 10px;
    margin-bottom: 10px;
}

.page1-top {
    max-width: 600px;
    font-size: 15px;
    line-height: 40px;
    min-height: 150px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.219);
    word-wrap: normal;
    margin-right: 10px;
    margin-left: 10%;
    margin-top: 10%;
    padding-bottom: 20px;

}

.page1-bottom {
    max-width: 600px;
    margin-top: 10%;
    margin-left: 10%;
}

.input-name {
    max-width: 600px;
    min-width: 300px;
    min-height: 60px;
    background-color: rgb(0, 0, 85);
    border-style: none;
    border: 4px solid rgb(66, 182, 66);
    border-radius: 10px;
    margin-top: 10px;
    color: white;
    padding-left: 10px;
    font-size: 15px;
    margin-right: 10px;
}

.page1-buttom {
    background-color: rgb(66, 182, 66);
    color: white;
    height: 50px;
    width: 120px;
    border-radius: 30px;
    margin-top: 30px;
    border: none;
    cursor: pointer;
    margin-right: 10px;
    font-weight: bold;
    font-size: 15px;
    text-decoration: none;
}

.page1-buttom:hover {
    background-color: rgb(105, 201, 105);
}