@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Open+Sans:wght@600&family=Poppins:wght@400;700;800&family=Roboto:wght@400;500;700&display=swap');

.main-page3 {
    color: white;
    font-family: 'Roboto', sans-serif;
    word-wrap: normal;
    margin-top: 3%;
    margin-left: 4%;
    margin-right: 10px;
    margin-bottom: 10px;
}

.page3-top {
    max-width: 550px;
    font-size: 15px;
    line-height: 40px;
    min-height: 150px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.219);
    word-wrap: normal;
    margin-right: 10px;
}


.page3-buttom {
    background-color: rgb(66, 182, 66);
    color: white;
    height: 50px;
    width: 120px;
    border-radius: 30px;
    margin-top: 30px;
    border: none;
    cursor: pointer;
    margin-right: 10px;
    font-weight: bold;
    font-size: 15px;
    text-decoration: none;
}

.page3-buttom-next {
    background-color: rgb(66, 182, 66);
    color: white;
    height: 50px;
    width: 160px;
    border-radius: 30px;
    margin-top: 30px;
    border: none;
    cursor: pointer;
    margin-right: 10px;
    font-weight: bold;
    font-size: 15px;
    text-decoration: none;
}






.page3-inputs {
    margin-top: 30px;
}



.page3-buttom:hover {
    background-color: rgb(105, 201, 105);
}

.page3-input {
    margin-bottom: 20px;
    font-size: 15px;
}


.input-field {
    width: 300px;
    min-height: 60px;
    background-color: rgb(0, 0, 85);
    border-style: none;
    border: 4px solid rgb(66, 182, 66);
    border-radius: 10px;
    margin-top: 10px;
    color: white;
    padding-left: 10px;
    font-size: 15px;
}